import React from 'react';
import './index.scss';
import axios from 'axios';
import { Spin } from 'antd';
import { Input } from 'antd';
import { Button } from 'antd';
import { CloseCircleFilled, PictureOutlined, PlayCircleOutlined } from '@ant-design/icons';
const { TextArea } = Input;

const wx = window.wx
const state = 'cl1'
const id = 'wx5224793b7dc7f7b7'
const wxURL = 'https://open.weixin.qq.com/connect/oauth2/authorize'
const auth = () => window.location.href = `${wxURL}?appid=${id}&redirect_uri=http://api.woaidakele.cn/api/${state}&response_type=code&scope=snsapi_userinfo&state=${state}#wechat_redirect`

export default class PageFeedback extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            // login: !(window.location.search.includes('code') && window.location.search.includes('state')),
            login: window.location.search.includes('code') && window.location.search.includes('state'),
            loading: true,
            imgList: [
                // 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                // 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                // 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                // 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg'
            ],
            history: [
                {
                    time: '2020-04.18 08:00',
                    feedback: '这是我的建议这是我的建议这是我的建议这 是我的建议`~`',
                    img: [
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg'
                    ],
                    // voice: 'https://www.w3school.com.cn/i/horse.ogg',
                    voice: '',
                    reply: '',
                    replyTime: '2020-04.18 08:00'
                },
                {
                    time: '2020-04.18 08:00',
                    feedback: '这是我的建议这是我的建议这是我的建议这 是我的建议`~`',
                    img: [
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg'
                    ],
                    voice: '',
                    reply: '您好，您的建议我们已经收到，感谢您 的反馈。1',
                    replyTime: '2020-04.18 08:00'
                },
                {
                    time: '2020-04.18 08:00',
                    feedback: '这是我的建议这是我的建议这是我的建议这 是我的建议`~`',
                    img: [
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg',
                        'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=139954460,3451817723&fm=26&gp=0.jpg'
                    ],
                    voice: '',
                    reply: '您好，您的建议我们已经收到，感谢您 的反馈。2',
                    replyTime: '2020-04.18 08:00'
                },
            ],
            newest: null
        }
    }
    componentDidMount() {
        if (this.state.login) {
            axios.get(`http://api.woaidakele.cn/api/${state}/wechat`).then(({ data }) => {
                console.log(data)
                wx.config(data)
                wx.ready(() => {
                    this.setState({
                        loading: false,
                        newest: this.state.history.find(e => e.reply !== '')
                    })
                })
                wx.error(() => {
                    console.log('网络可能出了点问题，退出重新进入试试？')
                })
            })
        } else {
            auth()
        }
    }
    chooseImage() {
        wx.chooseImage({
            sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
            sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
            success: ({ localIds }) =>  {
                this.setState({
                    imgList: localIds
                })
            }
        })
    }
    delImg(index) {
        this.state.imgList.splice(index, 1)
        this.setState({
            imgList: this.state.imgList
        })
    }
    
    render() {
        return <div className="PageFeedback">
            {
                this.state.login ?
                <div className="content">
                    <Spin spinning={ this.state.loading } size="large">
                        <header>
                            <div className="top">
                                <div className="left">
                                    <img src="http://api.woaidakele.cn/public/uploads/cl.jpg" alt="常来" />
                                </div>
                                <div className="right">
                                    <div className="top">你好！</div>
                                    <div className="bot">我是大卫博士品牌创始人：常来</div>
                                </div>
                            </div>
                            <div className="bot">如果你对我们的产品以及服务有什么意见或者建议，请在下方留言反馈给我们，帮助我们改进。</div>
                        </header>
                        <main>
                            <div className="title">写反馈</div>
                            <div className="input">
                                <TextArea placeholder="说点什么吧~" autoSize={true} maxLength={120} />
                                <div className="imgList">
                                    {
                                        this.state.imgList.map((url, index) => (<div className="imgItem" key={index}>
                                            <CloseCircleFilled onClick={() => this.delImg(index)} />
                                            <img src={url} alt={url} onClick={() => console.log('img:' + index)} />
                                        </div>))
                                    }
                                </div>
                            </div>
                            <div className="submit">
                                <div className="choose">
                                    <div className="item" onClick={ this.chooseImage }>
                                        <PictureOutlined />
                                        <span className="text">添加图片</span>
                                    </div>
                                </div>
                                <Button type="primary" onClick={() => console.log('提交')}>提交</Button>
                            </div>
                        </main>
                        {
                            this.state.newest && <section>
                                <div className="header">
                                    <div className="left">
                                        <img src="http://api.woaidakele.cn/public/uploads/cl.jpg" alt="常来" />
                                        <span>{ this.state.newest.replyTime }</span>
                                    </div>
                                    <div className="right">最新回复</div>
                                </div>
                                <main>{ this.state.newest.reply }</main>
                            </section>
                        }
                        <footer>
                            <div className="title">反馈历史</div>
                            {
                                this.state.history.length ?
                                this.state.history.map((item, index) => (<div className="item" key={index}>
                                    <div className="text">{ item.feedback }</div>
                                    <div className="img">{
                                        item.img.length ?
                                        item.img.map((url, index) => (<img src={url} alt={url} key={index} />)) :
                                        ''
                                    }</div>
                                    <div className="footer">
                                        {
                                            item.voice && <PlayCircleOutlined className="play" style={{ color: '#4BB5FF' }} onClick />
                                        }
                                        <div className="info">
                                            <span className="time">{ item.time }</span>
                                            {
                                                item.reply.length ?
                                                <span className="reply">已回复</span> :
                                                <span className="notReply">未回复</span>
                                            }
                                            
                                        </div>
                                    </div>
                                </div>)) :
                                <div>暂无反馈历史</div>
                            }
                        </footer>
                    </Spin>
                </div> :
                <div className="login">
                    <Button type="primary" onClick={() => auth()}>登陆</Button>
                </div>
            }
        </div>
    }
}
